



























import { Component, Vue } from 'vue-property-decorator';
import RoutineApi from '@/services/api/RoutineApi';
import Routine from '@/models/Routine';

const routineApi = new RoutineApi();

@Component
export default class RoutinesList extends Vue {
    private name: string;

    public constructor() {
        super();
        this.name = name;
    }

    public submit(): void {
        const routine = new Routine();
        routine.name = this.name;

        routineApi.post(routine);

        this.$router.push({ name: 'routine-list' });
    }
}
